.project-list {
    position: absolute;
    top: 0;
    left: 0;
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scrollbar-width: none;  /* Firefox */
    overflow-y: scroll;
    display: flex;
    height: 100vh;
    width: 100vw;
    justify-content: space-around;
    align-items: center;
    padding: 8vh 0.5rem;
    box-sizing: border-box;
    z-index: 0;
    flex-wrap: wrap;
} 
.project-list::-webkit-scrollbar { 
    display: none;  /* Safari and Chrome */
}
.project {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 22vw;
    max-width: 300px;
    border-radius: 20px;
    background-color: rgba(40, 40, 40, 0.5);
    backdrop-filter: blur(35px);
    transition-duration: 350ms;
    transition-timing-function: cubic-bezier(0.4, 0.64, 0, 1.4);
    animation: appear 300ms ease-out;
    animation-iteration-count: 1;
    padding: 0.15rem;
    overflow: hidden;
    font-size: 1rem;
}
.project header {
    max-height: 80%;
    border-radius: 20px 20px 0 0;
    border-bottom: none;
    transition-duration: 300ms;
    padding: 0.5rem 0.5rem;
    cursor: pointer;
    box-sizing: border-box;
}

.project-screen {
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 20px 20px 20px 20px;
    aspect-ratio: calc(16/10);
}
.project-details {
    /* height: 18%; */
    /* background-color: rgba(0, 0, 0, 0.7); */
    border-radius: 0 0 20px 20px;
    padding: 1% 2.5%;
    box-sizing: border-box;
    filter: blur(0px);
    color: white;
    display: flex;
    flex-direction: column;
    transition-duration: 300ms;
}

.project-title {
    font-size: 2.5em;
    font-weight: 600;
    /* padding: 0.1rem 0; */
    transition-duration: 300ms;
   /*  border-bottom: 3px solid rgb(106, 106, 106); */
    /* transform: translateY(50px); */
    width: fit-content;
    margin: 0 auto;
    line-height: 3rem;
    /* border-bottom: 2px solid white; */
}

.project-description {
    border-top: 3px solid rgba(106, 106, 106, 0.281);
    padding-top: 0.2rem;
    margin: 0.5rem;
    /* transform: translateY(150px); */
    transition-duration: 300ms;
    transition-timing-function: ease;
    transition-delay: 0;
    font-size: 0.9em;
    min-height: 5rem;
    color: grey;
}


.project:hover {
    background: linear-gradient(to right, rgb(234, 141, 20), purple);
    /* width: 450px; */
   /*  margin: 0 1rem; */
    min-width: 28vw;
}
.project:hover .project-description {
    color: rgb(188, 188, 188);
    
}
.project:hover .project-title {
    transform: translateY(0px);
    /* border-bottom: 1px solid white; */
    margin: 0 auto;
}

.project:hover header {
    background-color: rgb(29, 29, 29);
    max-height: 50%;
}

.project:hover .project-details {
    background-color: rgb(29, 29, 29);
    height: 50%;
}




.project::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    border-radius: 22px;
    /* background: rgba(255, 255, 255, 0.651) */ /* linear-gradient(to right, rgb(234, 141, 20), purple) */;
}

.project:hover::after {
    visibility: hidden;
    background: linear-gradient(to right, rgb(234, 141, 20), purple);
}

.project-links {
    display: flex;
    justify-content: end;
    gap: 0.5rem;
    margin-top: auto;
    margin-bottom: 1rem;
}

.project-links button {
    position: relative;
   /*  border: none;
    outline: 2px solid white; */
    background: linear-gradient(to right, rgb(234, 141, 20), purple);
    color: white;
    font-weight: 500;
    border-radius: 20px;
    padding: 0.2rem 0.7rem;
    font-family: inherit;
    font-size: 0.9rem;
    cursor: pointer;
    letter-spacing: 0.5px;
    box-sizing: content-box;
    margin-left: 0.2rem;
    filter: brightness(0.8);
    z-index: 2;
    border: 0;
    
}

.project-links button:hover {
    /* background: linear-gradient(to right, rgb(234, 141, 20), purple); */
    /* outline: none; */
    filter: brightness(1);
    outline: 2px solid aliceblue;
}

.project-links a {
    display: flex;
    text-decoration: none;
}
.project-links img {
    height: 24px;
    width: 24px;
    margin: auto;
    filter: brightness(0.7);
}

.project-links img:hover {
    filter: brightness(1);
    animation: rotate 300ms ease-in;
}

@keyframes rotate {
    0% {
        transform: rotate(0deg);
    }
    50% {
        transform: rotate(40deg);
    }
    100% {
        transform: rotate(0deg);
    }
}

@keyframes appear {
    0% {
        transform: scale(0.5);
    }
    100% {
        transform: scale(1);
    }
}

@media only screen and (min-width: 1350px) {
    .project-list {
        flex-wrap: nowrap;
    }
}

@media only screen and (max-width: 925px) {
    .project-description {
        color: rgb(188, 188, 188);
    }

    .project {
        backdrop-filter: blur(35px);
    }
}

@media only screen and (max-width: 500px) {
    .project:nth-last-child(1) {
        margin-bottom: 10vh;
    }
}