.bubble-wrapper {
    position: absolute;
    display: flex;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 0; 
    filter: url('#goo');
    -webkit-filter: url('#goo');
    overflow-y: hidden;
    overflow-x: hidden;
  }

.bubble {
  position: absolute;
  height: 7vh;
  width: 7vh;
  border-radius: 50%;
  background: linear-gradient(250deg, var(--purple), var(--orange));
  box-shadow: 0px 10px 13px -7px #000000;
  cursor: pointer;
}

 .bubble:hover {
    background: linear-gradient(250deg, white, var(--orange));
}


:root {
  --main-color: white;
  --purple: #ae00ff;
  --orange: #d36a2d;
  --blue: rgb(0, 17, 255);
}
