.navbar {
  position: fixed;
  width: 100vw;
  top: 0;
  left: 0;
  box-sizing: border-box;
  height: 7vh;
  opacity: 0;
  padding: 0px 15% 0px 10%;
  color: white;
  display: flex;
  -webkit-animation: appearNav 500ms linear 2s forwards;
          animation: appearNav 500ms linear 2s forwards;
  z-index: 1;
 /*  justify-content: space-between; */
  background-color: rgba(40, 40, 40, 0.5);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}



.navbar span {
  margin: auto 0;
  box-sizing: border-box;
  font-size: 28px;
}

.navbar-contact {
  color: white;
  color: var(--main-color);
  padding: 0 1rem;
  margin: auto 0;
  background: none;
  outline: none;
  border: none;
  font-size: 22px;
  border-radius: 15px;
  border: 2px solid white;
  border: 2px solid var(--main-color);
  box-sizing: border-box;
  font-family: inherit;
  font-weight: 400;
  cursor: pointer;
  justify-self: end;
  align-self: end;
  transition: all 0.3s;
}

.navbar-contact:hover {
  background-color: white;
  background-color: var(--main-color);
  color: black;
  transition: all 0.3s;
}

.navbar-logo {
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.navbar-links {
  margin: auto 0 auto auto;
  display: flex;
}

.navbar a {
  display: flex;
  margin: auto;
}

.contact-link {
  height: 32px;
  -webkit-filter: saturate(0);
          filter: saturate(0);
}

.contact-link:hover {
  -webkit-filter: saturate(1);
          filter: saturate(1);
  transition-duration: 200ms;
}

#github {
  margin: auto 0 auto auto;
  padding: 0.3rem;
  box-sizing: border-box;
}

.coming-soon {
  -webkit-filter: blur(5px);
          filter: blur(5px);
}


#github:hover {
  -webkit-animation: rotate 300ms ease-in;
          animation: rotate 300ms ease-in;
}

@-webkit-keyframes rotate {
  0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
  }
  50% {
      -webkit-transform: rotate(40deg);
              transform: rotate(40deg);
  }
  100% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
  }
}

@keyframes rotate {
  0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
  }
  50% {
      -webkit-transform: rotate(40deg);
              transform: rotate(40deg);
  }
  100% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
  }
}
