.svg-blur {
  visibility: hidden;
  height: 0;
  width: 0;
}

.control-panel {
  background-color: rgba(50, 50, 50, 0.5);
  border-radius: 8px;
  margin: auto auto 2rem auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  box-sizing: content-box;
  align-items: center;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border: 1px solid rgb(255, 255, 255);
  opacity: 0;
  -webkit-animation: appearNav 500ms linear 2s forwards;
          animation: appearNav 500ms linear 2s forwards;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  overflow: hidden;
}
.control-panel a {
  flex: 1 1;
  opacity: 0;
  -webkit-animation: panel-appear 1.5s ease-out 2s forwards;
          animation: panel-appear 1.5s ease-out 2s forwards;
  padding: 0.4rem 2rem; 
  margin: 0;
  color: white;
  text-decoration: none;
  height: 100%;
  display: flex;
  transition-duration: 200ms;
}

.link-active {
  background:white;
  color: white;
}

.link-active span {
  color: black;
}

@-webkit-keyframes panel-appear {
  0% {
    padding: 0.4rem 0rem;
    opacity: 0;
  }

  40% {
    padding: 0.4rem 0rem;
  }

  60% {
    opacity: 0;
  }

  100%{
    padding: 0.4rem 2rem;
    opacity: 1;
  }
}

@keyframes panel-appear {
  0% {
    padding: 0.4rem 0rem;
    opacity: 0;
  }

  40% {
    padding: 0.4rem 0rem;
  }

  60% {
    opacity: 0;
  }

  100%{
    padding: 0.4rem 2rem;
    opacity: 1;
  }
}

