  margin: auto;
  font-size: 1.2rem;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  /* animation: control-text-appear 1.5s ease-out; */
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}


@-webkit-keyframes control-text-appear {
