.project-list {
    position: absolute;
    top: 0;
    left: 0;
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scrollbar-width: none;  /* Firefox */
    overflow-y: scroll;
    display: flex;
    height: 100vh;
    width: 100vw;
    justify-content: space-around;
    align-items: center;
    padding: 8vh 0.5rem;
    box-sizing: border-box;
    z-index: 0;
    flex-wrap: wrap;
} 
.project-list::-webkit-scrollbar { 
    display: none;  /* Safari and Chrome */
}
.project {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 22vw;
    max-width: 300px;
    border-radius: 20px;
    background-color: rgba(40, 40, 40, 0.5);
    -webkit-backdrop-filter: blur(35px);
            backdrop-filter: blur(35px);
    transition-duration: 350ms;
    transition-timing-function: cubic-bezier(0.4, 0.64, 0, 1.4);
    -webkit-animation: appear 300ms ease-out;
            animation: appear 300ms ease-out;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    padding: 0.15rem;
    overflow: hidden;
    font-size: 1rem;
}
.project header {
    max-height: 80%;
    border-radius: 20px 20px 0 0;
    border-bottom: none;
    transition-duration: 300ms;
    padding: 0.5rem 0.5rem;
    cursor: pointer;
    box-sizing: border-box;
}

.project-screen {
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 20px 20px 20px 20px;
    aspect-ratio: calc(16/10);
}
.project-details {
    /* height: 18%; */
    /* background-color: rgba(0, 0, 0, 0.7); */
    border-radius: 0 0 20px 20px;
    padding: 1% 2.5%;
    box-sizing: border-box;
    -webkit-filter: blur(0px);
            filter: blur(0px);
    color: white;
    display: flex;
    flex-direction: column;
    transition-duration: 300ms;
}

.project-title {
    font-size: 2.5em;
    font-weight: 600;
    /* padding: 0.1rem 0; */
    transition-duration: 300ms;
   /*  border-bottom: 3px solid rgb(106, 106, 106); */
    /* transform: translateY(50px); */
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
    line-height: 3rem;
    /* border-bottom: 2px solid white; */
}

.project-description {
    border-top: 3px solid rgba(106, 106, 106, 0.281);
    padding-top: 0.2rem;
    margin: 0.5rem;
    /* transform: translateY(150px); */
    transition-duration: 300ms;
    transition-timing-function: ease;
    transition-delay: 0;
    font-size: 0.9em;
    min-height: 5rem;
    color: grey;
}


.project:hover {
    background: linear-gradient(to right, rgb(234, 141, 20), purple);
    /* width: 450px; */
   /*  margin: 0 1rem; */
    min-width: 28vw;
}
.project:hover .project-description {
    color: rgb(188, 188, 188);
    
}
.project:hover .project-title {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    /* border-bottom: 1px solid white; */
